body{background:#f3f3f3}.form{background:#fff;max-width:320px;margin:auto;margin-top:100px;padding:20px;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23);border-radius:4px}.btn{padding:5px 40px}.btn:hover{box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.error{color:#dc3545;display:block;font-size:13px;line-height:1;padding-top:5px}.links{padding:5px;padding-right:0;padding-top:15px;display:flex;justify-content:space-between;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)}
