.reactModal{position:absolute;margin:auto;left:0;right:0;top:20%}.modalForm{position:relative;margin:auto;width:300px;background:white;border-radius:5px;text-align:center;animation:openModal .4s;margin-bottom:20px}.modalForm h2{border:1px solid silver;padding:5px 0;margin-bottom:35px;border-radius:5px 5px 0 0}.modalForm input{border-radius:5px;border:1px solid silver;padding:5px 0}.modalForm button{margin:35px 0;padding:6px 50px}.faTimes{font-size:20px;position:absolute;right:10px;top:14px;transition:all .3s ease}.faTimes:hover{color:red;cursor:pointer;transform:rotate(180deg)}@keyframes openModal{0%{opacity:0}100%{opacity:1}}
