.topNav a{color:#333;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24);padding-right:40px;font-size:20px;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)}.topNav a:hover{text-decoration:none;color:#666}.topNav span{color:red;border:1px solid black;border-radius:5em;padding:4px 8px;margin-left:5px;background:#ff8080;font-size:14px;color:white}.tasks{margin-top:20px;display:flex;flex-direction:column}.reactModal{position:absolute;top:40px;left:40px;right:40px;bottom:40px;width:500px;height:500px;background-color:white;border-radius:5px;text-align:center;margin:auto}
