body{background:#f3f3f3}.form{background:#fff;max-width:320px;margin:auto;margin-top:40px;padding:20px;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23);border-radius:4px}.btn{padding:5px 40px}.btn:hover{box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.error{color:#dc3545;display:block;font-size:13px;line-height:1;padding-top:5px}.links{padding:5px;padding-right:0;padding-top:15px;display:flex;justify-content:space-between;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)}.logoWrapper,.h2{text-align:center}.logoWrapper h1,.h2 h1{font-size:36px;padding-right:40px;padding-bottom:10px;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24);font-style:italic}.logoWrapper{margin-top:20px;margin-bottom:10px}.logo{width:70px;margin:auto}.h2{font-size:26px;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24);font-style:italic}
