.main{padding-bottom:50px;position:relative}.main strong{font-size:24px;padding-top:20px;display:block}.main ul{padding-left:30px;padding-top:20px}.main li{list-style-type:circle;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24)}.sucfull{color:green;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24);font-size:40px;padding-left:20px}.h4{width:40%;font-size:20px}.h4 span{color:green;text-shadow:0 1px 3px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.24)}.img{width:500px;position:absolute;right:0;top:100px}.img img{border-radius:4px;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23);cursor:pointer}.img img:hover{box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}
